
import { render } from '@testing-library/react';
import { Space, Table } from 'antd';
import React from 'react';
const columns = [
    {
        title: '',
        dataIndex: 'id',
        key: 'id',
    },
    {
        title: '',
        dataIndex: 'question',
        key: 'question',
        render: (text) => <a>{text}</a>,
    },
    {
        title: '',
        dataIndex: 'options',
        key: 'options',
        render: (options) => options.join(', ')
    },
    {
        title: '',
        dataIndex: 'answer',
        key: 'answer',
    },
    {
        title: '',
        key: 'action',
        render: (_, record) => (
            <Space size="middle">
                <a></a>
                <a></a>
            </Space>
        ),
    },
];
const data = [
    {
        key: '1',
        id: '1',
        question: 'React',
        options: ['A. ', 'B. ', 'C. '],
        answer: 'A'
    },
    {
        key: '2',
        id: '2',
        question: 'React',
        options: ['A. componentDidMount', ' B. componentDidUpdate', ' C. componentWillUnmount'],
        answer: 'A'
    },
    {
        key: '3',
        id: '3',
        question: 'React',
        options: ['A. Redux', ' B. MobX', ' C. Context API'],
        answer: 'A'
    },
    {
        key: '4',
        id: '4',
        question: 'React',
        options: ['A. ', ' B. ', 'C. '],
        answer: 'A'
    },
    {
        key: '5',
        id: '5',
        question: 'React',
        options: ['A. React Router', 'B. Next.js', 'C. Reach Router'],
        answer: 'A'
    }
];

const onChange = (pagination, filters, sorter, extra) => {
    console.log('params', pagination, filters, sorter, extra);
};

const App = () => <Table
    columns={columns}
    dataSource={data}
    pagination={{
        current: 1, // 
        pageSize: 5, // 
        total: 100, // 
        showSizeChanger: false,
        position: ['bottomLeft'],
        onChange: () => { /*  */ },
    }}
    onChange={onChange} />;
export default App;