import { Space, Table } from 'antd';
import React from 'react';
const columns = [
    {
        title: '',
        dataIndex: 'id',
        key: 'id',
    },
    {
        title: '',
        dataIndex: 'name',
        key: 'name',
        render: (text) => <a>{text}</a>,
    },
    {
        title: '',
        dataIndex: 'date',
        key: 'date',
    },
        {
        title: '',
        dataIndex: 'password',
        key: 'password',
    },

    {
        title: '',
        key: 'action',
        render: (_, record) => (
            <Space size="middle">
                <a></a>
                <a></a>
            </Space>
        ),
    },
];
const data = [
    {
        key: '1',
        id: '1',
        name: 'John Brown',
        date: '2025-9-11',
        password: '123456',
    },
    {
        key: '2',
        id: '2',
        name: 'Jim Green',
        date: '2025-9-11',
        password: '123456',
    },
    {
        key: '3',
        id: '3',
        name: 'Joe Black',
        date: '2025-9-11',
        password: '123456',
    },
    {
        key: '4',
        id: '4',
        name: 'Joe Black',
        date: '2025-9-11',
        password: '123456',
    },
    {
        key: '5',
        id: '5',
        name: 'Joe Black',
        date: '2025-9-11',
        password: '123456',
    }
];

const onChange = (pagination, filters, sorter, extra) => {
    console.log('params', pagination, filters, sorter, extra);
};

const App = () => <Table
    columns={columns}
    dataSource={data}
    pagination={{
        current: 1, // 
        pageSize: 5, // 
        total: 100, // 
        showSizeChanger: false,
        position: ['bottomLeft'],
        onChange: () => { /*  */ },
    }}
    onChange={onChange} />;
export default App;